@import "./stake.css";
@media all and (min-width: 0px) {
  .nav-wrap {
    /*background: #673ab7;*/
    background: #070707a4;
    /*var(--color1);*/
    padding: 25px;
    display: flex;
    justify-content: center;
    position: relative;
  }
  .nav-wrap ul {
    display: flex;
    justify-content: center;
  }
  @media only screen and (max-width: 992px) {
    .nav-wrap ul {
        display: flex;
        justify-content: center;
        flex-direction: column;
        position: fixed;
        transform: translateY(-100%);
        top: 0;
        background-color: #000;
        width: 100%;
        align-items: center;
        padding: 3rem 0;
        transition: all 0.4s ease;
      }

      .nav-wrap ul.active {
        transform: translateY(0%);
        transition: all 0.4s ease;
      }
  }
  .nav-wrap ul li {
    margin: 0 1rem;
  }
  @media only screen and (max-width: 992px) {
    .nav-wrap ul li {
        margin: 1rem;
      }
  }
  .nav-wrap ul li a {
    color: var(--white);
    font: 2.5rem/4rem var(--font1);
    text-decoration: none;
  }
  .mintbox {
    display: flex;
    text-align: center;
    justify-content: center;
    font: 2.5rem/4rem var(--font1);
    text-decoration: none;
    height: 65px;
    width: 110px;
    color: #fff;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background: linear-gradient(to bottom, #31313100 0%, #31313100 100%);
    color: #fff;
    text-decoration: none;
    font-size: 30px;
    font-weight: 500;
  }
  .homebox {
    /* background: linear-gradient(to bottom, #ff5722 0%, #ff6699 100%);*/
    background: linear-gradient(to bottom, #0ba4b8 0%, #0ba4b8 100%);

    border-radius: 20px;
    width: 100px;
    text-align: center;
  }
  .docbox {
    background: linear-gradient(to bottom, #0ba4b8 0%, #0ba4b8 100%);
    border-radius: 20px;
    width: 100px;
    text-align: center;
  }
  .stakebox {
    display: flex;
    text-align: center;
    justify-content: center;
    font: 2.5rem/4rem var(--font1);
    text-decoration: none;
    height: 80px;
    width: 100px;
    color: #fff;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background: linear-gradient(to bottom, #31313100 0%, #31313100 100%);
    color: #fff;
    text-decoration: none;
    height: 65px;
    width: 110px;
  }
  .logo {
    /* background: white; */
    width: 65px;
    height: 60px;
    margin-right: 600px;
  }

  @media only screen and (max-width: 992px) {
    .logo {
        margin-right: auto;
      }
  }

  .hamburger {
    z-index: 99;
    position: relative;
  }

  @media only screen and (min-width: 992px) {
    .hamburger {
      display: none;
    }
  }

  .hamburger .checkbox {
    position: absolute;
    display: block;
    height: 32px;
    width: 32px;
    top: 20px;
    right: 0;
    z-index: 5;
    opacity: 0;
    cursor: pointer;
  }

  .hamburger .hamburger_lines {
    display: block;
    height: 21px;
    width: 32px;
    position: absolute;
    top: 23px;
    right: 10px;
    z-index: 2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines .line {
    display: block;
    height: 3px;
    width: 85%;
    margin-left: auto;
    border-radius: 10px;
    background: #fff;
  }

  .hamburger .hamburger_lines .line1 {
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    -webkit-transition: -webkit-transform 0.4s ease-in-out;
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines .line2 {
    -webkit-transition: -webkit-transform 0.2s ease-in-out;
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  }

  .hamburger .hamburger_lines .line3 {
    -webkit-transform-origin: 0% 100%;
    transform-origin: 0% 100%;
    -webkit-transition: -webkit-transform 0.4s ease-in-out;
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines.active {
    right: 5px;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines.active .line {
    background: #fff;
  }

  .hamburger .hamburger_lines.active .line1 {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  .hamburger .hamburger_lines.active .line2 {
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
  }

  .hamburger .hamburger_lines.active .line3 {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
}

