:root {
    --color1: #060099;
    --color2: #C51E52;
    --color3: #012515;
    --color4: #427a8b;
    --color5: #8E9AAF;
    --color6: linear-gradient(to bottom, #0ba4b8a9 0%, #0ba4b8a9 100%);
    --color9: linear-gradient(to bottom, #0ba4b8a9 0%, #0ba4b8a9 100%);
    --color7: linear-gradient(to bottom, #0ba4b8a9 0%, #0ba4b8a9 100%);
    --color8: linear-gradient(to bottom, #0ba4b8 0%, #0ba4b8 100%);
    --white: #FFFFFF;
    --black: #000000;
    --font1: 'Boogaloo', sans-serif;
    --font2: 'Poppins', sans-serif;
    background: #000000;
    background: var(--black);
    background-size: contain;
    background-image: url("https://i.gifer.com/XzZg.gif"); 
   /* background-image: url("https://i.gifer.com/2iiH.gif"); */

}

@media all and (min-width: 0px) {
    .stake-app {
        height: 120vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
     .connect-btn{
        background: linear-gradient(to bottom, #0ba4b8 0%, #0ba4b8 100%);
        background: var(--color8);
        font: 1.125rem/1.6rem 'Boogaloo', sans-serif;
        font: 1.125rem/1.6rem var(--font1);
        color: #FFFFFF;
        color: var(--white);
        border-radius: 12px;
        border: none;
        height: 50px;
        width: 136px;
        margin-bottom: 2rem;
    } 
    .stake-app h1 {
        font: 2.5rem/4rem 'Boogaloo', sans-serif;
        font: 2.5rem/4rem var(--font1);
        color: #FFFFFF;
        color: var(--white);
        font-weight: bold;
    }
    .stake-app h2 {
        font: 1.125rem/1.8rem 'Poppins', sans-serif;
        font: 1.125rem/1.8rem var(--font2);
        color: #FFFFFF;
        color: var(--white);
        font-weight: bold;
        margin-bottom: 2rem;
    }
    .card-container {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        max-width: 90%;
        width: 550px;
        height: 550px;
        margin: 0 auto;
        /* border: 3px solid var(--white); */
        border-radius: 20px;
        transition: 0.5s all;
        background: linear-gradient(to bottom, #0ba4b8a9 0%, #0ba4b8a9 100%);
        background: var(--color6)
    }
    .card-container:hover {
        -webkit-transform: scale(1.005);
                transform: scale(1.005);
        transition: 0.5s all;
    }
    .btn {
        background: linear-gradient(to bottom, #0ba4b8a9 0%, #0ba4b8a9 100%);
        background: var(--color7);
        color: #FFFFFF;
        color: var(--white);
        font: 1rem/1rem 'Boogaloo', sans-serif;
        font: 1rem/1rem var(--font1);
        border-radius: 12px;
        font-weight: bold;
        outline: none;
        border: none;
        height: 56px;
        width: 100%;
    }
    .mb-5 {
        margin: 1rem 0;
    }
    .form {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    }
    .form input {
        height: 56px;
        max-width: 40%;
        width: 50%;
        outline: none;
        border: none;
        border-radius: 12px 0 0 12px;
        font: 1.125rem/1.8rem 'Poppins', sans-serif;
        font: 1.125rem/1.8rem var(--font2);
        color: #000000;
        color: var(--black);
        text-align: center;
    }
    .form button {
        border-radius: 0 12px 12px 0;
        height: 56px;
        width: 50%;
        margin: 1rem 0;
    }
    .form input::-webkit-input-placeholder {
        padding: 0 0.5rem;
        font: 0.8rem/1rem 'Poppins', sans-serif;
        font: 0.8rem/1rem var(--font2);
        color: #000000;
        color: var(--black)
    }
    .form input::placeholder,
    .form input:focus {
        padding: 0 0.5rem;
        font: 0.8rem/1rem 'Poppins', sans-serif;
        font: 0.8rem/1rem var(--font2);
        color: #000000;
        color: var(--black)
    }
    .card-container div div {
        display: flex;
        justify-content: space-between;
        width: 90%;
        margin: 0 auto;
    }
    .reward {
        background: #8E9AAF;
        background: var(--color5);
        border: 1px solid #FFFFFF;
        border: 1px solid var(--white);
        font: 0.8rem/1rem 'Poppins', sans-serif;
        font: 0.8rem/1rem var(--font2);
        font-weight: 700;
        border-radius: 12px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 50px;
        width: 90%;
        margin: 0 auto 5px;
        color: #FFFFFF;
        color: var(--white);
    }
    .reward span {
        padding: 0 0.5rem 0 0;
    }
    .stake-app p {
        margin: 1rem 0 2rem;
        font: 1rem/1.6rem 'Poppins', sans-serif;
        font: 1rem/1.6rem var(--font2);
        color: #FFFFFF;
        color: var(--white);
        font-weight: 500;
    }
    .stake {
        color: #0000;
    }
}

@media all and (min-width: 768px) {
    .form input {
        width: 55%;
    }
    .form button {
        width: 50%;
    }
}
