    text-decoration: none;
    height: 65px;
    width: 110px;
  }
  .logo {
    /* background: white; */
    width: 65px;
    height: 60px;
    margin-right: 600px;
  }

  @media only screen and (max-width: 992px) {
    .logo {
        margin-right: auto;
      }
  }

  .hamburger {
    z-index: 99;
    position: relative;
  }

  @media only screen and (min-width: 992px) {
    .hamburger {
      display: none;
    }
  }

  .hamburger .checkbox {
    position: absolute;
    display: block;
    height: 32px;
    width: 32px;
    top: 20px;
    right: 0;
    z-index: 5;
    opacity: 0;
    cursor: pointer;
  }

  .hamburger .hamburger_lines {
    display: block;
    height: 21px;
    width: 32px;
    position: absolute;
    top: 23px;
    right: 10px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: all 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines .line {
    display: block;
    height: 3px;
    width: 85%;
    margin-left: auto;
    border-radius: 10px;
    background: #fff;
  }

  .hamburger .hamburger_lines .line1 {
    -webkit-transform-origin: 0% 0%;
    transform-origin: 0% 0%;
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines .line2 {
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  }

  .hamburger .hamburger_lines .line3 {
    -webkit-transform-origin: 0% 100%;
    transform-origin: 0% 100%;
    transition: -webkit-transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines.active {
    right: 5px;
    transition: all 0.4s ease-in-out;
  }

  .hamburger .hamburger_lines.active .line {
    background: #fff;
  }

  .hamburger .hamburger_lines.active .line1 {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }

  .hamburger .hamburger_lines.active .line2 {
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
  }

  .hamburger .hamburger_lines.active .line3 {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
  }
}

